body {
  background-color: #000;
  color: #fff;
  font-family: 'Arial', sans-serif;
}

.app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

h1 {
  color: #FFD700;
  margin: 0;
}

.search-bar {
  padding: 10px;
  border: none;
  border-radius: 5px;
  width: 200px;
}

.filter, .sort {
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #FFD700;
  color: #000;
  margin: 0 10px;
}

.cart {
  background-color: #333;
  padding: 20px;
  border-radius: 5px;
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
}

.product-card {
  background-color: #222;
  padding: 20px;
  border-radius: 5px;
  text-align: center;
}

.product-card img {
  max-width: 100%;
  border-radius: 5px;
}

.product-card h3 {
  color: #FFD700;
  margin: 15px 0;
}

.product-card p {
  color: #fff;
  font-size: 18px;
}

.buttons {
  display: flex;
  justify-content: space-around;
}

.buy-button, .cart-button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.buy-button {
  background-color: #FFD700;
  color: #000;
}

.cart-button {
  background-color: #444;
  color: #FFD700;
}
